import React from 'react'
import logo from '../assets/logo.svg'

const Header = () => (
    <div className="App-header container-full">
        <img src={logo} className="App-logo" alt="logo" />
        <h1 className="App-title">React todo app</h1>
    </div>
)

export default Header


